<template>
    <div id="div">
        <h1>动画</h1>
        <div class="cartoon">
            <div class="left">
                <Video v-for="(item,index) in reList" :key="index" @click.native="goto(item)" :up="item.author" :date="item.date" :title="item.title" :img="item.cover"></Video>
            </div>
            <div class="right">
                <h1>排行榜</h1>
                <el-table
                    :data="tableData"
                    stripe
                    style="width: 300px">
                    <el-table-column
                    prop="name"
                    width="250">
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import Video from '../components/Video.vue'
export default {
    components:{
        Video
    },
    async created(){
        const {data:res}=await this.$axios.get('/api/lists',{params:{type:'cartoon'}}) 
        this.reList=res.data
    },
    data(){
        return{
            tableData: [{
                name: '1，名侦探柯南',
                }, {
                name: '2，黑执事',
                }, {
                name: '3，clannad',

                }, {
                name: '4，福尔摩斯',
                },{
                name: '5，命运石之门',
                },{
                name: '6，数码宝贝',
                },{
                name: '7，精灵宝可梦',
                },{
                name: '8，蜡笔小新',
            }],
            reList:[]
        };
    },
    methods:{
        goto(item){
            this.$router.push({ path: '/video',query:{id:item}});
        }
    }
}
</script>
<style lang="less" scoped>
#div{
    margin: 0 50px;
    h1{
        font-size: 20px;
        margin-bottom: 10px;
    }
    .cartoon{
        height: 400px;
        display: flex;
        .left{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            width: 1100px;
            height: 400px;
        }
        .right{
            height: 400px;
            width: 270px;
            padding-left: 50px;
            h1{
                margin-top: -35px;
            }
            .el-table{
                margin-top: -30px;
                z-index: -1;
            }
        }
    }
}
</style>