<template>
  <div>
      <img @mouseenter.stop="enter" @mousemove="enter" @mouseleave="leave" @mouseout.stop="leave" :src="img">
      <p>{{title}}</p>
      <span><i class="el-icon-coordinate"></i>{{up}} <span>- {{date}}</span></span>
      <div class="shadow" ref="imgs"><i class="el-icon-video-play"></i></div>
  </div>
</template>

<script>
export default {
    props:["img",'title','up','date'],
    methods:{
        enter(){
            this.$refs.imgs.style="display:block"
        },
        leave(){
            this.$refs.imgs.style="display:none"
        }
    }
}
</script>

<style lang="less" scoped>
div{
    position: relative;
    width: 250px;
    height: 200px;
    cursor: pointer;
    margin-bottom: 15px;
    margin-right: 16px;
    img{
        width: 250px;
        height: 110px;
        border-radius: 10px;
        margin-bottom: 5px;
    }
    p{
        height: 50px;
        font-size: 15px;
    }
    p:hover{
        color: #5093E4;
    }
    span{
        font-size: 14px;
        color: #9DA2A8;
        i{
            margin-right: 5px;
        }
        span{
            margin-left: 5px;
        }
    }
    span:hover{
        color: #5093E4;
    }
    .shadow{
        display: none;
        top: 0;
        left: 0;
        width: 250px;
        position: absolute;
        height: 110px;
        text-align: center;
        line-height: 110px;
        background-color: rgba(0,0,0,0.5);
        i{
            color: white;
            height: 100px;
            width: 100px;
        }
    }
}
</style>