<template>
  <div class="app">
      <div class="nav"><Nav></Nav></div>
      <div class="mainbox">
          <h1>{{$route.query.id.title}}</h1>
      <video :src="src" controls></video>
      <div class="zan">
          <img :src="path" @click="zan">
            <span>{{zan1}}</span>
        </div>
        <div class="comm">
            <textarea v-model="command"></textarea>
            <button @click="comand">发表评论</button>
        </div>
        <div class="commarea">
            <el-card v-for="(item,index) in commands.split('-=').filter(i=>i!='')" :key="index">
                {{item}}
                <!-- <el-button type="error" size="mini">删除评论</el-button> -->
                <el-popconfirm title="确定删除评论吗？" @confirm="del(item)">
                    <el-button slot="reference" size="mini">删除</el-button>
                </el-popconfirm>
            </el-card>
        </div>
        
      </div>
      <Bottom></Bottom>
  </div>
</template>

<script>
import Nav from '../components/Nav.vue'
import Bottom from '../components/Bottom.vue'
export default {
    components:{
        Nav,
        Bottom
    },
    created(){
       this.commands=this.$route.query.id.comment
    },
    mounted(){
        
    },
    data(){
        return{
            src:'http://localhost:8080/video?videoId='+this.$route.query.id.videoID,
            path:require("../../public/imgs/dianzan.png"),
            zan1:Number(this.$route.query.id.zan),
            command:'',
            commands:''
        };
    },

    methods:{
        async zan(){
            this.zan1++;
            this.path=require("../../public/imgs/dianzan2.png")
            const {data:res} = await this.$axios.get('/api/updatezan',{params:{videoID:this.$route.query.id.videoID,zan:this.zan1}})
            if(res.states=='true')
                this.$message.success("点赞成功！！")
            else
                this.$message.error("点赞失败失败！！")
        },
        async comand(){
            let temp=this.command+"-="+this.commands
            this.commands=temp
            this.command=''
            console.log(temp)
            const {data:res} = await this.$axios.get('/api/updatecom',{params:{videoID:this.$route.query.id.videoID,commands:temp}})
            if(res.states=='true')
                this.$message.success("发表评论成功！！")
            else
                this.$message.error("发表评论失败！！")
        },
        async del(item){
            if(item=='') return
            let temp=this.commands.replace(item+"-=","").replace(item,"")
            console.log(temp)
            const {data:res} = await this.$axios.get('/api/updatecom',{params:{videoID:this.$route.query.id.videoID,commands:temp}})
            this.commands=temp
            if(res.states=='true')
                this.$message.warning("删除成功！！")
            else
                this.$message.error("删除失败！！")
        }
    }
}
</script>

<style lang="less" scoped>
    .app{
        width: 100%;
        .nav{
            height: 65px;
            background-color: skyblue;
        }
        .mainbox{
            margin: 0 300px;
            video{
            width: 800px;
            margin-top: 20px;
            }
            h1{
                margin-top: 50px;
                font-size: 23px;
                font-weight: 400;
            }
            .zan{
                margin-top: 10px;
                vertical-align: middle;
                padding-left: 10px;
                img{
                    width: 30px;
                }
                span{
                    display: inline-block;
                    margin-left: 10px;
                }
            }
            .comm{
                display: flex;
                padding-top: 30px;
                textarea{
                    padding: 5px;
                    width: 700px;
                    height: 60px;
                    resize: none;
                    font-size: 18px;
                }
                button{
                    width: 73px;
                    height: 73px;
                    padding: 0 15px;
                    margin-left: 10px;
                    border-radius: 5px;
                    outline: none;
                    border: 0;
                    color: white;
                    background-color: #00A1D6;
                    cursor: pointer;
                }
                button:hover{
                    background-color: rgb(50, 50, 150);
                }
            }
            .commarea{
                padding-top: 20px;
                width: 795px;
                .el-card{
                    margin: 5px 0;
                    width: 795px;
                }
                .el-button{
                    float: right;
                }
            }
        }
    }
</style>