<template>
  <div id="outer">
      <el-card class="box-card">
        <el-tabs v-model="activeName">
                <el-tab-pane label="登录" name="first">
                    <el-form ref="form" :model="user" :rules="rules" :label-position="labelPosition" label-width="60px">
                        <el-form-item label="账号" prop="account">
                            <el-input
                                placeholder="请输入账号"
                                prefix-icon="el-icon-place"
                                v-model="user.account"
                                size="small">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="密码" prop="password">
                            <el-input
                                placeholder="请输入密码"
                                prefix-icon="el-icon-unlock"
                                v-model="user.password"
                                show-password
                                size="small">
                            </el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button @click="reset" type="success" size="small" round plain>重置</el-button>
                            <el-button @click="login" type="primary" size="small" round plain>登录</el-button>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="注册" name="second">
                    <el-form :model="user" :rules="rules" :label-position="labelPosition" label-width="60px">
                        <el-form-item label="账号" prop="account">
                            <el-input
                                placeholder="请输入账号"
                                prefix-icon="el-icon-place"
                                v-model="user.account"
                                size="small">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="密码" prop="password">
                            <el-input
                                placeholder="请输入密码"
                                prefix-icon="el-icon-unlock"
                                v-model="user.password"
                                show-password
                                size="small">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="确认" prop="password">
                            <el-input
                                placeholder="请输入密码"
                                prefix-icon="el-icon-unlock"
                                v-model="tempPassword"
                                show-password
                                size="small">
                            </el-input>
                        </el-form-item>
                        <el-form-item>
                            <button @click.prevent="zhuce" class="zhuce">注册/登录</button>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
      </el-card>
  </div>
</template>

<script>
export default {
     data() {
      return {
        activeName: 'first',
        labelPosition: 'right',
        tempPassword:"",
        user:{
            account:'',
            password:'',
            state:'login'
        },
         rules: {
            account: [
                { required: true, message: '请输入账号名称', trigger: 'blur' },
                { min: 3, max: 6, message: '长度在 3 到 6 个字符', trigger: 'blur' }],
            password: [
                { required: true, message: '密码不少于10位', trigger: 'blur' },
                { min: 6, max: 10, message: '长度在 6 到 10 个字符', trigger: 'blur' }]
        }
      };
    },
    methods:{
        login(){
            this.$refs.form.validate(async (boolean,object)=>{
                if(boolean){
                    const {data:res} = await this.$axios.get("/api/login",{params:this.user})
                if(res.states=='true'){
                    this.$store.state.logined=true
                    this.$router.push({ path: '/'});
                }
                else{
                    this.$message({
                        showClose: true,
                        message: '登陆失败，用户名或密码错误',
                        type: 'error'
                    });
                }
            }
            })
            if(this.user.account==''||this.user.password=='') return
            
        },
        zhuce(){
            if(this.user.account==''||this.user.password==''||this.tempPassword=='') return
            if(this.user.password!=this.tempPassword){
                this.$message({
                    showClose: true,
                    message: '两次输入密码不一致！！',
                    type: 'warning'
                });
                return
            }
            this.user.state='';
            this.$refs.form.validate(async (boolean,object)=>{
                if(boolean){
                    console.log(boolean)
                    const {data:res} = await this.$axios.get("/api/login",{params:this.user})
                if(res.states=='repeatn'){
                    this.$message({
                        showClose: true,
                        message: '用户名已存在！',
                        type: 'warning'
                    });
                    return
                }
                if(res.states=='success'){
                    this.$message({
                        showClose: true,
                        message: '注册成功！！',
                        type: 'sucess'
                    });
                    this.$store.state.logined=true
                    setTimeout(()=>{
                        
                        this.$router.push({path:'/'})
                    },1000)
                    return
                }
                if(res.states=='faild'){
                    this.$message({
                        showClose: true,
                        message: '注册失败！！',
                        type: 'warning'
                    });
                    return
                }
                }
                else{
                    return
                }
            })

        },
        reset(){
            this.$refs.form.resetFields();
        }
    }
}
</script>

<style scoped>
#outer{
    width: 700px;
    margin: 190px auto;
}
.box-card{
    display: flex;
    height: 320px;
    justify-content: center;
    background-color: #FEFEFE;
    background-image: url(https://s1.hdslb.com/bfs/seed/jinkela/short/mini-login/img/22_open.72c00877.png),url(https://s1.hdslb.com/bfs/seed/jinkela/short/mini-login/img/33_open.43a09438.png);
    background-repeat: no-repeat,no-repeat;
    background-size: 15%;
    background-position: 0 100%,100% 100%;
}
.el-tabs{
    text-align: center;
    width: 300px;
}
.zhuce{
    width: 155px;
    height: 30px;
    margin-left: -50px;
    background-color: #0093C4;
    border: 0;
    color: white;
    border-radius: 10px;
    cursor: pointer;
}
.zhuce:hover{
    background-color: blue;
}
.el-button{
    width: 100px;
}
</style>